import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import styles from '../dragAndDrop.module.css';
import { TrashIcon } from '../../../components/styled/Icons';

const StyledDraggableContainer = styled.div``;
const StyledDropZone = styled.div``;
const StyledDragOverlay = styled.div`
        height: 5%;
        position: fixed;
        top: 8%;
        left: 48%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 1000;
`;

export const useDraggable = () => {

    const Draggable = (props) => {

        const handleDrag = (e, { data, handleDragStart }) => {

            if (data)
                e.dataTransfer.setData('data', JSON.stringify(data));

            if (handleDragStart)
                handleDragStart();
        }

        const handleDragEnd = (e, { handleDragEnd }) => {
            if (handleDragEnd)
                handleDragEnd();
        }

        return (
            <React.Fragment>
                <StyledDraggableContainer
                    draggable
                    style={props.style}
                    onDragStart={(e) => handleDrag(e, props)}
                    onDragEnd={(e) => handleDragEnd(e, props)}
                >
                    {props.children}
                </StyledDraggableContainer>
            </React.Fragment>
        )
    }

    Draggable.propTypes = {
        style: PropTypes.object,
        data: PropTypes.object,
        handleDragStart: PropTypes.func,
        handleDragEnd: PropTypes.func
    }

    const overlay = ({ showFlag, onDrop }) => {
        if (!showFlag) {
            return null;
        }
        const handleDrop = (e, passedOnDropCallback) => {
            const data = JSON.parse(e.dataTransfer.getData('data'));

            if (passedOnDropCallback)
                passedOnDropCallback(data);
        }

        return (

            <StyledDragOverlay  >
                <div
                    id='drop-aria-circle'
                    className={styles.overlayWrapper}
                    onDragOver={(e) => e.preventDefault()}
                    onDrop={(e) => { e.preventDefault(); handleDrop(e, onDrop) }}>
                    <StyledDropZone
                        className={styles.dropArea}>
                        <TrashIcon height="3em" width="3em" fill="#efefef"></TrashIcon>
                    </StyledDropZone>
                </div>
            </StyledDragOverlay >

        )
    }

    const DragOverlay = (props) => {
        return ReactDOM.createPortal(
            <React.Fragment>
                {overlay({ ...props })}
            </React.Fragment>,
            document.querySelector("#drag-overlay-root")
        )
    }

    DragOverlay.propTypes = {
        showFlag: PropTypes.bool.isRequired,
        onDrop: PropTypes.func.isRequired
    }
    return [Draggable, DragOverlay];
}
